    position: relative;
    border: none;
    outline: none;
    font-weight: 700;
    font-size: 12px;
    font-family: sans-serif;
    text-decoration: underline;
    color: inherit;
    background: transparent;
    transition: transform 0.2s ease-in-out;
}

._3UDbi::after {
    content: "";
    width: 0.7em;
    height: 0.7em;
    display: block;
    position: absolute;
    right: 0;
    transform: rotate(45deg);
    transform-origin: center;
}

